
import { useState } from "react"
import C from "./Current_company.module.css"
import { useContext } from "react"
// import img from '../../../img/zuk.png'
import ContextCurrent from "../../../Consumer/ContextCurrent"

function Current_company(props){
    const [company_inform, setCompany_inform]=useState(true)
  
 
const consumer = useContext(ContextCurrent)

    return(
        
        <>
        
            {company_inform&&<div className={C.main_conteiner_profile}>
            <div className={C.conteiner_profile}>
        <div className={C.conteiner_profile_logNu}>
            <img alt="img" onError={(e)=>{consumer.Eror_img(e.target)}} src={props.img}/>
            <div className={C.num_web}>
                <a href={props.website}>{props.website}</a> 
            {props.phone}
            </div>
           
        </div>
        <button onClick={()=>{consumer.close_company(setCompany_inform)}} className={C.newFail_button}>X</button>
        <div className={C.conteiner_profile_description}>
        <p className={C.profile_description}>
            {props.description}
            </p>            
        </div>
        </div>
       
    </div>}
        </>
        )
}

export default Current_company