import C from "./All_comper.module.css"
import img from '../../../img/zuk.png'
import {useState } from "react"




function ComperMini(props){


    const[close_open, setClose_open]=useState(false)


    const Eror = e => {
        e.target.src = img
    }
    let color =""

    if(+props.item.profile.changesPercentage>0){
        color="green"
    }else if(+props.item.profile.changesPercentage<0){
        color="red"
    }else{
        color="black"
    }

let color_price=""
if(props.item.profile.price>0){
    color_price="green"
}else if(props.item.profile.price<0){
    color_price="red"
}else{
    color_price="black"
}


const delate=()=>{
    
const arr_str = props.info_company.filter(item=>item!==props.item.symbol)

    props.setResult(props.result.filter(item=>item.symbol!==props.item.symbol))
    props.setInfo_company(arr_str)
    window.history.pushState({}, null, "/#/comper_company/"+arr_str.join('&'))
}


const close=()=>{
    setClose_open(true)
}

const open=()=>{
    setClose_open(false)
}
// 
    return(<div onMouseEnter={close} onMouseLeave={open} onDoubleClick={delate} className={C.information_company}>
        {close_open&&<button onClick={delate} className={C.close}>X</button>}
        <div className={C.img_comp}>
           <img alt="img"  onError={Eror}  src={props.item.profile.image}/> 
        </div>
        
        <div className={C.name_company}>{props.item.profile.companyName}</div>
        <div className={C.symbol_company}>{props.item.symbol}</div>
        <div style={{color : color_price}} className={C.price_global}>{props.item.profile.price}</div>
        <div className={C.chages_company}>{props.item.profile.changes}</div>
        <div style={{color}} className={C.chagesPercent_global}>{props.item.profile.changesPercentage}</div>
        <div className={C.ceo_company}>{props.item.profile.ceo}</div>
        
        </div>)
}
export default ComperMini