import Serch from "./Serch/Serch"
import GlobalComonentInp from "./Global_component_inp/Global_comonent_inp";
import ComponentBtn from "./Global_result/Component_btn";
import C from "./Main.module.css"
import Comper from "./Comper/Comper";
import Limit from "./Limit/Limit";
import FastLine from "./Fast_line/Fast_line";
import { useCallback, useEffect, useState } from "react"
import localforage from "localforage";
import Modal from 'react-modal';
import ContextFORcomper from "../../Consumer/ContextFORcomper";



const history = localforage.createInstance({
  name: "history"
});


function Main({
  hook_value,
  set_all_global_result_inp,
  spiner_inp,
  fast_line,
  all_result_btn,
  get_global_result_inp,
  set_all_result_btn,
  get_result_btn,
  setHook_value,
  pathname,
  spiner_but,
  all_global_result_inp,
  params_symbol

}){
     
     
    
  
    const [comper, setComper]=useState([])
    const [comper_value, setComper_value]=useState(2)
    const [iSlimit, setiSlimit]=useState(false)

    const customStyles = {
      content: {
        border: "none",
        background: "transperent"
      },
    };
    
const get_history_item = useCallback(()=>{
  history.getItem("current_value").then(e=>{
    if(!e)return
    setComper_value(e)
  })
  
  history.getItem('company').then(e=>{
    if(!e)return
    setComper(e)
    
  })
  
  
},[]) 

  useEffect(get_history_item, [get_history_item])

  useEffect(()=>{
    Modal.setAppElement('body')
  

//   if(!hook_value){
//  set_all_global_result_inp([])
// }
},[hook_value,spiner_inp,set_all_global_result_inp])



const button_add=(symbol)=>{
  
  if(comper.includes(symbol)) return
  if(comper.length>=comper_value){
    setiSlimit(true)
    
    return
    
  } 
  setComper([...comper,symbol]) 
history.setItem("company", [...comper,symbol])
}
  
const delate=(symbol)=>{
  const arr=comper.filter(item=>symbol!==item)

setComper(arr)
history.setItem("company",arr)
}





    return(
      <>
      {fast_line&&<FastLine all_result_btn={all_result_btn}/>}
      
      <ContextFORcomper.Provider value={{
     delate
    
    }} > 
        <Comper setComper={setComper} comper={comper} comper_value={comper_value} setComper_value={setComper_value} history={history}/>

        </ContextFORcomper.Provider>
        
          <Serch 
          get_global_result_inp={get_global_result_inp}
          set_all_global_result_inp={set_all_global_result_inp} 
          set_all_result_btn={set_all_result_btn} 
          get_result_btn={get_result_btn}
          history={history}
          comper_value={comper_value}
          value={hook_value}
          setValue={setHook_value}
          pathname={pathname}
          params_symbol={params_symbol}
          
          />
        
        
        <Modal
        isOpen={!!iSlimit}
        style={customStyles}
        >
          <Limit setComper_value={setComper_value} comper_value={comper_value} setiSlimit={setiSlimit} comper={comper} history={history}/>
          </Modal>
        

           <main className={C.containe_search_result}>
        

       
 
       <ComponentBtn all_result_btn={all_result_btn}
        comper={comper}
        setComper={setComper}
        button_add={button_add}
        isStart={spiner_but}
        value={hook_value}
        
        />
        
        

       
       <GlobalComonentInp 
        all_global_result_inp={all_global_result_inp} isStart={spiner_inp} value={hook_value}/>
        
        </main>
      </>
    )
}
export default Main