import {Line} from 'react-chartjs-2'
import Spiner_HOC from '../Spiner/Spiner_HOC';
// import propTypes from "prop-types"

function History_company(props){
  //  if(!props.history.historical) return
  

const data_c=props.history.map(item=>item.close).reverse()
const close=props.history.map(item=>item.date).reverse()

    const data = {
        labels: close,
        
        datasets: [{
          label:'',
            data: data_c,
            backgroundColor: 
            
            'rgba(55, 06, 86, .2)',
            
            borderColor: 
            'rgba(5, 99, 132, 1)',
            
            
            borderWidth: 1,
            
        }]
      };
      
      const options = {
        
        scales: {
          yAxes: [
            
            {
              ticks: {
                beginAtZero: true,
              },
            },
          ],
        },
      };


   return(<div>
     
       <Line data={data} options={options} />       
   </div>)
 
}

// History_company.propTypes={
