import axios from 'axios'


const instons = axios.create({
baseURL:'https://stock-exchange-dot-full-stack-course-services.ew.r.appspot.com/api/v3/'
})


const check_response= (item)=>{
   // console.log(item.data)
   return item.status===200
    ? item.data
    : Promise.reject(item)
}





 const All_axios =  {
    get_arr : (value) =>  instons.get(`search?query=${value.toUpperCase()}&limit=10&exchange=NASDAQ`)
 .then(check_response)
    
    ,
    get_current_symbol:(symbol)=>instons.get(`company/profile/${symbol}`) 
    .then((response)=>check_response(response)),
    all:axios.all,


    get_history:(symbol)=>instons.get(`historical-price-full/${symbol}?serietype=line`)
    .then(check_response)
    
    

}
export default All_axios







