import { useEffect, useState } from 'react';
import ComperMini from './Comper_mini';
import C from "./All_comper.module.css"
import { Redirect } from 'react-router';
import { NavLink } from 'react-router-dom';
import propTypes from "prop-types"
import All_axios from '../../../axios/axios';





function All_comper(props){
// console.log(props)

const [info_company, setInfo_company]=useState([])
const [result, setResult]=useState([])




//!!!!!!
console.log(result)

useEffect(()=>{
    // console.log(props.match.params.symbols)
    if(props.match.params.symbols){
      const arr= props.match.params.symbols.split("&")
    setInfo_company(arr)

    arr.map(item=> All_axios.get_arr(item))
    All_axios.all(arr.map(item=>All_axios.get_current_symbol(item)))
    .then((e)=>{setResult(e)})  
    }
    

},[props.match.params.symbols])

    return(<div className={C.company}>
{!props.match.params.symbols&&<Redirect to="/Erors"/>}



         <NavLink to={"/"}>
                 <button className={C.back}>Back</button>
            </NavLink> 

<div className={C.container_data_global}>

   
    <div className={C.img_company}>Img:</div>
    <div className={C.name_company}>Name:</div>
    <div className={C.symbol_company}>Symbol:</div>
    <div className={C.price_global}>Prices:</div>
    <div className={C.chages_company}>Changes:</div>
    <div className={C.chagesPercent_global}>ChangesPercent:</div>
    <div className={C.ceo_company}>Ceo_company:</div>
</div>
        <div className={C.container_data_all}>
            {result.map(item=><ComperMini key={item.symbol} result={result} symbol={item.symbol} item={item} setResult={setResult} info_company={info_company} setInfo_company={setInfo_company} />)}
        </div>
    </div>)
}

All_comper.propTypes={
   match: propTypes.shape({
       isExact: propTypes.bool,
       params: propTypes.shape({
           symbols: propTypes.string
       })
   }),



//    new_Error_props : function(prop, name, component){
//     console.log(prop.location.state);
//     if(prop.location.state !== undefined) {
//         return new Error('Not Undefined')
//     }
//    },
//    history: propTypes.shape({
//        length: propTypes.number
//    })

}


export default All_comper 