import { useState } from "react"
import C from "./Setting.module.css"


function Current_setting_for_element(props) {
    const [data_style, setData_Style] = useState(props.teg)
    const change_color = (e) => {

        props.setSetting(props.setting.map(item => {
            if (item._id === props.teg._id) {
                return data_style
            }
            return item
        }))

    }
    const current_change = (e) => {
    
        // if(e.target.name==="color"){
        //     console.log(data_style)
        //     setData_Style(
        //         {...data_style,
        //             color:e.target.value
        //         }
        //     )
        // }

        setData_Style({ ...data_style, [e.target.name]: e.target.value })
        }


    return (<div className={C.setting_main}>
        <div>
            <button onClick={props.change_plus} className={C.butt}>+</button>
        </div>



        <h2>Name url {props.teg?.name}</h2>
        <div>
            <div>
                <div>color text</div>
                <input type="color" name="color" onBlur={change_color} onChange={current_change} value={data_style.color} />

            </div>
            <div>
                <div>color background</div>
                <input type="color" onChange={current_change} name="background" onBlur={change_color}  value={data_style.background} />
            </div>
            <div>
                <div>Font size</div>
                <input type="number" onBlur={change_color}  name="fontSize" onChange={current_change} value={data_style.fontSize} />
            </div>
            <div>
                <div>text font family</div>
                <select  onChange={current_change}  onBlur={change_color} name="fontFamily" id="" value={data_style.fontFamily}>
                    <option value={"Stick No Bills"}>Stick No Bills</option>
                    <option value={"Titillium Web"}>Titillium Web</option>
                    <option value={"Birthstone"}>Birthstone</option>
                </select>
            </div>
        </div>
    </div>)
}
export default Current_setting_for_element