import { useEffect, useState } from 'react';
import HistoryCompany from './History_company';
import DescriptionCompany from './Description_company';
import All_axios from '../../axios/axios';
import C from "./Company.module.css"
import localforage from "localforage";


function Company(props){
 
    const[company_result, setCompany_result]=useState({})
    
    const[background, setBackground]=useState(true)   
    // const [b_color, setB_color]=useState("white")

    const [history, setHistory]=useState([])
useEffect(()=>{
  All_axios.get_current_symbol(props.match.params.symbol)
       .then(setCompany_result)
},[props.match.params.symbol])

useEffect(()=>{
  All_axios.get_history(props.match.params.symbol)
       .then(setHistory)

},[props.match.params.symbol])



const color_local=localforage.createInstance({
    background:"background"
})

useEffect(()=>{
        color_local.getItem("background")
        .then((e)=>{
          
          if(e===null)return

          setBackground(e)
          
        })
    },[color_local])

 


const change_background=()=>{

 setBackground(!background)
 color_local.setItem('background',!background)

}

//!сперва мы отрисовываем спинер, после чего проверяем symbol на пустосту и рисуем наши элементы 

   return(<>
   <div 
   style={{background:background ? 'white' : 'grey'}}  
   className={C.global}>

       <DescriptionCompany change_background={change_background} isStart={!company_result.symbol} company_result={company_result}/>
       <HistoryCompany isStart={!history?.historical?.length}  history={history.historical} />
        
        
    </div>
    </>)
}

export default Company