import C from "./Component_btn.module.css"
import img from '../../../img/zuk.png'
import CurrentCompany from "../Current_company/Current_company";
import { useState } from "react";
import { NavLink } from "react-router-dom";
import ContextCurrent from "../../../Consumer/ContextCurrent";



function Result_btn(props){
const [company,setCompany]=useState(false)

const Eror=e=>{
e.target.src=img
// console.clear()
}
let color =""

if(+props.profile.changesPercentage>0){
    color="green"
}else if(+props.profile.changesPercentage<0){
    color="red"
}else{
    color="black"
}

const Eror_img=e=>{
    
    e.src=img
    } 


const current_company=()=>{
    setCompany(true)
}

const close_company=(setCompany_inform)=>{
    setCompany(false)
    setCompany_inform(false)
} 

// load.addEventListener("error", (e)=>{console.clear(); console.log(e, 'error');});

    return(

        <div className={C.current_actions}>
       <NavLink to={"/company/"+props.symbol}>
        <img alt="img" onError={Eror} src={props.profile.image} className={C.img_pr}/>
       </NavLink>
           

        
       
        {props.profile.website!==""
        ?<a href={props.profile.website} className={C.company_name}>{props.profile.companyName}</a>
        :<p className={C.company_name}>{props.profile.companyName} </p>
        
        }
        
        <div className={C.price_color}>

            <p className={C.prices} style={{color}}>
            {props.profile.changesPercentage}
        </p>
       
        <p>{props.profile.price}$</p>
        </div>
        

        <div className={C.button_company_div}>
            <button className={C.button_company} onClick={current_company}>Company</button>

            {company&&
            <ContextCurrent.Provider value={{
                Eror_img,
                close_company
            }}>
                <CurrentCompany setCompany={setCompany} img={props.profile.image} phone={props.profile.phone} website={props.profile.website} description={props.profile.description}/>
            </ContextCurrent.Provider>
             }
            <button 
            // 
            disabled={props.comper.includes(props.symbol)}
            
            onClick={()=>props.button_add(props.symbol)} className={C.button_add}>+</button>
        </div>
       
        
        
    </div>
    
    
    )
}
export default Result_btn