import C from "./Limit.module.css"
import change_limit from "../../../helps/limit_is"
function Limit(props){
const close_limit=()=>{
   props.setiSlimit(false)
}



const focus=(e)=>{
    change_limit(e,props,'limit')
    close_limit()
}

    return<div className={C.global_limit}>
        <div className={C.limit}>
            {!+props.comper_value
            ?"Change Value"
            :"Change Limit"
            }
            
            
        <input type="number" onBlur={focus}  max="99" min="2" className={C.input_limit} value={props.comper_value} onChange={e=>props.setComper_value(e.target.value)}/>
        
        <button className={C.button_close} onClick={close_limit}>X</button>
        </div>
       

    </div>
}
export default Limit