import { useEffect} from "react"
import C from "./Serch.module.css"



function Serch({
  history,
  setValue,
  get_result_btn,
  pathname,
  get_global_result_inp,
  set_all_global_result_inp,
  value,
  params_symbol
  
  
}){
  const first_function=()=>{
    // console.log(params_symbol)
    if(params_symbol){
      setValue(params_symbol)
      get_result_btn(params_symbol)
      return
    }


    
      history.getItem("symbol")
    .then((e)=>{
      if(e){
        setValue(e)
        get_result_btn(e)
       
     
        
      }else{
        if(pathname.slice(1).trim()){
          setValue(pathname.slice(1))
          get_result_btn(pathname.slice(1))
        }
      }   
    })
    

    

  }

  
// console.log(1);

  useEffect(()=>{
    
    first_function()
},[])


const local=(e)=>{
 history.setItem("symbol", e)
}

const change_input=(e)=>{
  // disble=false
//  e.target.value=window.location
window.history.pushState({}, null, "/#/"+e.target.value)


     if(e.target.value.trim()){
         
        let lim=300
    const interval=setInterval(time,100)
    const value = e.target.value
    
    function time(){
     lim -= 100
     if(value !== e.target.value){
         return clearInterval(interval) 

     }
         
         if(lim <= 0){
           get_global_result_inp(e.target.value)
           return clearInterval(interval) 
        } 
        
    } 
    
}else{
  
  set_all_global_result_inp([])
}

setValue(e.target.value)
local(e.target.value)
}


const serch=()=>{
get_result_btn(value)

}

const serch_enter=(e)=>{
  // console.log(e)
}

return(
    <div className={C.header}>
   <input className={C.main_input} type="text"
   value={value}
    onChange={change_input}/>
    <button  disabled={!value} className={C.serch} onKeyDown={serch_enter} onClick={serch}>Serch</button>
    </div>
)
    
}
export default Serch