import Main_container from "../Main/Main_container";
import Company from "../Company/Company"
import All_comper from "../Main/All_comper/All_comper";
import Erors from "../../Erors/Erors";
import { Switch, Route } from "react-router-dom";
import HOC_setting from "../Setting/HOC_setting";
import SettingContainer from "../Setting/SettingContainer";
import { useContext } from "react";
import Context_setting from "../Setting/Context_setting";

function TopNavigation(){

    const consumer=useContext(Context_setting)
    

//сперва сами придумываем массив, в котором будет obj с компонентами и path из за которого будет рисовать эту компоненту
//беру этотмассив и с помощу map(перебирать )
//! главная задача создать ROUTE под каждую компоненту(под ключ Comp)
//при переборе этого массива после создание каждого компоннета роута добавлеям свой path и свой key
// после чего дописываем метод render который будет рисовать ту компоненту которая находится в нем
//создаем переменную Component которая равнеятя HOC
//HOC принимает в первом (компоненту) во втором пропсы который запихаем в эту компоннету. в случаии второго вызова первый аргумент приходит от бибилотеке react-router-dom,
//второй аргумент приходит от consumer
//после чего возвращаем переменнуб COMPON в качестве компоненты которая заменяет route
//рисуем All_rout в Switch
    const All_rout =   [
        {
          Comp :  Company,
          path : '/company/:symbol?'
        },
        {
            Comp :All_comper,
            path : "/comper_company/:symbols?"
        },
        {
            Comp : SettingContainer,
            path : "/setting"
        },
        {
            Comp : Erors,
            path : "/Erors"
        },
        {
            Comp : Main_container,
            path : "/:symbol?"
        }
    ].map(data =><Route path={data.path} key={data.path}  render={prop=>{
    const COMPON  =  HOC_setting(data.Comp)(prop, consumer)
    return <COMPON/>
         }} />
    )

    return(
        <>


        <Switch>
            {
              All_rout
            }

        </Switch>
        
          
        
        </>
        
    )
    
}


export default  TopNavigation

