import C from "./Company.module.css"
import img from '../../img/zuk.png'
import { NavLink } from "react-router-dom"
import Spiner_HOC from "../Spiner/Spiner_HOC"


function Description_company(props) {
        
    let color = ""
    if (+props.company_result.profile.changes > 0) {
        color = "green"
    } else if (+props.company_result.profile.changes < 0) {
        color = "red"
    } else {
        color = "black"
    }
    const Eror = e => {
        e.target.src = img
    }

    const back=()=>{
        console.log("description_company20")
    }

    


    return (
        <div  className={C.serch_result}>

            <a className={C.current_result_company} href={props.company_result.profile.website}>

                <img alt="eror" src={props.company_result.profile.image} onError={Eror} className={C.all_img} />


                <div>{props.company_result.profile.companyName}</div>
            </a>

            <div className={C.stock_price}>
                <div>Stock price:{props.company_result.profile.price}</div>
                <div style={{ color }}>{props.company_result.profile.changes}</div>
            </div>
            <button onClick={props.change_background} className={C.background}>Change background</button>

            <div className={C.description_info}>
                {props.company_result.profile.description}
            </div>
            <NavLink to={"/"}>
                <button onClick={back} className={C.back}>Back</button>
            </NavLink>
            
        </div>
        
    )
}
export default Spiner_HOC(Description_company) 