const change_limit=(e,props,ar)=>{
 

let value=+e.target.value




     if(props.comper.length >1){
        if(props.comper.length>+e.target.value) {
         
          value=props.comper.length
        }
    
    }
   if(value<2){

     value=2
   }
   if(value>99){
     value=99
   }
   
   props.setComper_value(value)
   props.history.setItem("current_value",value)
   
}
export default change_limit