import C from "./Fast_line.module.css"
import propTypes from 'prop-types'


function CurrentFastLine(props){
 


    let color = ""

        if(+props.item>0){
            color="green"
        }else if(+props.item<0){
            color="red"
        }else{
            color="black"
        }




return(
    <div className={C.stock_line}>
    <div className={C.corrent_symbol}>
    <p>{props.symbol}</p>
    <p style={{color}} className={C.line_price}>{props.item}</p>
    </div>

        </div>
)
}

CurrentFastLine.propTypes = {
    symbol : propTypes.string.isRequired,
    item: propTypes.string
}

export default CurrentFastLine