import Main from "./Main"
import { useCallback, useState } from "react"
import All_axios from "../../axios/axios"



function Main_container(props){
  

    const [all_global_result_inp,set_all_global_result_inp]=useState([])
    const [all_result_btn, set_all_result_btn]=useState([])
    const [spiner_inp, setSpiner_inp]=useState(false)
    const [spiner_but, setSpiner_but]=useState(false)
    const [fast_line, setFast_line]=useState(false)
    const [hook_value, setHook_value]=useState("")

    const get_global_result_inp=(value)=>{
 
        setSpiner_inp(true)
        All_axios.get_arr(value)       
      
           .then(set_all_global_result_inp)
           .finally(()=>setSpiner_inp(false))
         }
 
       
         //!btn
         const get_result_btn=useCallback((value)=>{ 
           let Error_data = []
 set_all_global_result_inp([])
           setSpiner_but(true)
    All_axios.get_arr(value)

 
      .then(data=>{
       
        Error_data = data
       All_axios.all(
         data.map(item=> All_axios.get_current_symbol(item.symbol)))
        .then(set_all_result_btn)
 
      })
      .catch((e)=>{
        console.log(e)
        console.log(Error_data);
      })
      .finally(()=>setSpiner_but(false),setFast_line(true))
  },[])  //?
          
          
 
 
  

  


    return <Main get_global_result_inp={get_global_result_inp} get_result_btn={get_result_btn} set_all_global_result_inp={set_all_global_result_inp} spiner_inp={spiner_inp} all_result_btn={all_result_btn}
    fast_line={fast_line}
    hook_value={hook_value}
    setHook_value={setHook_value}
    pathname={props.location.pathname}
    all_global_result_inp={all_global_result_inp}
    spiner_but={spiner_but}
    params_symbol={props.match.params.symbol}
    />

    
}
export default Main_container




