// import C from "./Setting.module.css"
import { useState } from "react"
import "./Plus.css"
import {generate as id} from 'shortid'

function Plus(props){

    const [value, setValue]=useState("")



    const close_window=()=>{
        props.setPlus(false)
    }
    const change_value=(e)=>{
        setValue(e.target.value)
    }
    
    const created=()=>{

        if(!(value in props.setting)){
            props.setSetting(
            [...props.setting,{
            color:"black", 
                [value]:value,
                name:value,
                _id:id()
            
            }  ])
           
        }
        props.setPlus(false)

        
    }
   
    return(<div className="global_window">
        <div className={props.plus? "plus_window active" :"plus_window"}>
            <div>Created new teg</div>
           <input value={value} onChange={change_value} type="text"/> 
           <button onClick={created}>Created</button>
            <button  onClick={close_window} className="close">X</button>
        </div>
        
    </div>)
}
export default Plus