import C from "./Fast_line.module.css"
import CurrentFastLine from "./Current_fast.line"
function Fast_line(props){

    
    const global_symbol=props.all_result_btn
    .filter(item=>item.symbol)
    .map(item=>  <CurrentFastLine symbol={item.symbol} item={item.profile.changesPercentage} key={item.symbol} />)

    return(
        <div className={C.stock_line_container}>
         {global_symbol}
        </div>

    )
}

export default Fast_line