import ResultBtn from "./Result_btn"
import Spiner_HOC from "../../Spiner/Spiner_HOC"
function Component_btn(props){
  
    if(props.value.trim() && !props.all_result_btn.length){
        return <div> no find</div>
    }

    if(props.all_result_btn.length===0) return (<div></div>)
    const global_symbol=props.all_result_btn
    .filter(item=>item.symbol)
    .map(item=>  <ResultBtn symbol={item.symbol} profile={item.profile} key={item.symbol} setComper={props.setComper} comper={props.comper} button_add={props.button_add} />)
    
    return(

        <>
            {global_symbol}
        </>
    )
}
export default  Spiner_HOC(Component_btn)



