import { useContext, useEffect, useState } from "react"
import Current_setting_for_element from "./Current_setting_for_element"
import Plus from "./Plus"
import C from "./Setting.module.css"
import Context_setting from "./Context_setting"






const SettingContainer = props => {
    const consumer = useContext(Context_setting)

    const [arr_current_teg, setArr_current_teg] = useState([])
    const [plus, setPlus] = useState(false)


    const change_plus = () => {
        setPlus(true)
    }

    useEffect(() => {

        setArr_current_teg(consumer.setting.map(item => <Current_setting_for_element setSetting={consumer.setSetting} setting={consumer.setting} change_plus={change_plus} key={item._id} teg={item} />))



        // for(const obj in  consumer.setting){
        //      arr.push(<Current_setting_for_element change_plus={change_plus}  key={consumer.setting[obj]._id} teg={consumer.setting[obj]}/>)
        //      }
        //      setArr_current_teg(arr)

    }, [consumer.setting])
    // console.log(arr_current_teg);

    return (
        <div className={C.main} >
            <div className={C.pp}>Setting</div>

            {plus && <Plus setting={consumer.setting} setSetting={consumer.setSetting} setPlus={setPlus} plus={plus} />}

            <div className={C.scroll}>
                {arr_current_teg}
            </div>

        </div>
    )
}

{/* <button>Save</button> */}
export default SettingContainer